.app__aboutus {
  position: relative;
}

.app__aboutus-overlay {
  position: absolute;
  inset: 0;
}

.app__aboutus-overlay img {
  width: 391px;
  height: 415px;
  z-index: 0;
}

.app__aboutus-content {
  width: 100%;
  z-index: 2;
}

.app__aboutus_about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  text-align: right;
}
.app__aboutus_history {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
}

.app__aboutus_about p,
.app__aboutus_history p {
  margin: 2rem 0;
  color: var(--color-grey);
}

.app__aboutus_knife {
  margin: 2rem 4rem;
}

.app__aboutus_knife img {
  height: 910px;
}

@media screen and (min-width: 2000px) {
  .app__aboutus_about p,
  .app__aboutus_history p {
    margin: 4rem 0;
  }
  .app__aboutus_knife img {
    height: 1110px;
  }
}

@media screen and (max-width: 900px) {
  .app__aboutus-content {
    flex-direction: column;
  }
  .app__aboutus_knife {
    margin: 4rem 0;
  }
}

@media screen and (max-width: 650px) {
  .app__aboutus-overlay img {
    width: 80%;
    height: 360px;
  }
}
